import tool from '../../static/base/toolFun.js';
import uniCountdown from '@dcloudio/uni-ui/lib/uni-count-down/uni-count-down.vue';
export default {
  components:{
    uniCountdown
  },
  data() {
    return {
      downSecond:0,
      btnClick:false,
      detail:{},
      show:false,
      bn:0,
    };
  },
  onLoad:function(options){
    this.bn = options.groups_bn
    this.getGroupDetail(options.groups_bn)
  },
  methods: {
    submitOrder(){
      const _this = this
      const data = {
        actSign:'is_group',
        activity_id:this.detail.GroupOrder.groups_id,
        quantity:1,
        type:'fastbuy',
        groups_bn:this.bn,
        sku_id:this.detail.Group.sku_id
      }
      this.$http({
        aurl: 'addCart',
        data: data,
        success: res => {
          uni.setStorage({
            key: 'type',
            data: 'fastbuy',
            success: function() {
              _this.needTokenLink('/pages/checkout/index');
            }
          });
        },
        fail: error => {
          uni.showToast({
            title: error.message.msg,
            icon: 'none'
          });
          console.log(error);
        }
      })
    },
    timeUp() {
      uni.showToast({
        title: '活动结束,下次早点来',
        icon: 'none'
      });
      this.btnClick = false;
    },
    getTime(time){
      let now_time = tool.toTimeStamp(tool.toFullTime()) / 1000;
      let end_time = tool.toTimeStamp(time) / 1000;
      return end_time - now_time;
    },
    getGroupDetail(bn){
      this.$http({
        aurl:'groupGoodsGroup',
        params:{
          groups_bn:bn
        },
        success:res => {
          this.detail = res.data.result
          this.downSecond = this.getTime(this.detail.GroupOrder.end_time)
          this.btnClick = true
          this.show = true
        },
        fail:error => {
          console.log(error)
        }
      })
    },
  },
  mounted() {
  },
};
